.App-wrapper {
    margin: 0 auto;
    display: grid;
    width: 1200px;

    grid-template-areas:
    "h h"
    "n c";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 100px 10fr;
    grid-gap: 10px;

}


.app-wrapper-content{
    grid-area: c;
    background-color: #246cb8;

}
.content img {
    height: 100%
}
